$sprite-cell-size: 40px;

// don't override top & bottom margin
.icon {
	margin-right: $icon-spacing;

	&:first-child {
		margin-left: 0; }

	&:only-child {
		margin-right: 0; }

	&:last-child {
		margin-right: 0; } }

@mixin icon-sprite($pos-x, $pos-y, $icon-width, $icon-height) {
	background-position: (-($sprite-cell-size * $pos-x)) (-($sprite-cell-size * $pos-y));
	width: $icon-width;
	height: $icon-height; }

@mixin icon($pos-x, $pos-y, $icon-width: 20px, $icon-height: 20px) {
	@include icon-sprite($pos-x, $pos-y, $icon-width, $icon-height);
	display: inline-block;
	text-indent: 105%;
	white-space: nowrap;
	padding: 0 !important;
	overflow: hidden;
	vertical-align: middle;
	font-size: 0; }

